.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 750px;
  padding: 58px 24px 0;
  margin: 0 auto 32px;
}

.logo {
  height: 6rem;
  width: auto;
  margin-bottom: 1rem;
}

.header {
  font-size: 48px;
  font-weight: bold;
  color: var(--white-color);
  text-align: center;
  margin-bottom: 20px;
}

.formContainer {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
  max-width: 458px;
  padding: 24px;
  background-color: var(--white-color);
  border-radius: 10px;
  box-shadow: 0px 4px 4px 0px #00000050;
}

.button {
  width: 100%;
  height: 60px !important;
  background-color: var(--primary-color);
}

.buttonCancel {
  width: 100%;
  height: 60px !important;
  background-color: var(--white-color);
  border-style: solid;
  border-color: var(--primary-color);
}

.textbuttonCancel {
  font-size: 16px;
  font-weight: bold;
  color: var(--primary-color);
  text-align: center;
}

.textbuttonConfirm {
  font-size: 16px;
  font-weight: bold;
  color: var(--white-color);
  text-align: center;
}

.input {
  height: 60px;
  padding: 0 18px;
}

.subHeader {
  font-size: 26px;
  color: var(--white-color);
  text-align: center;
  margin-bottom: 52px;
}
